.group {
  height: 20px;
  position: relative;
  width: 20px;
}

.group .overlap-group {
  height: 7px;
  left: 0;
  position: absolute;
  top: 11px;
  width: 20px;
}

.group .ellipse {
  background-color: var(--secondary-colorsecondary-2);
  border-radius: 6px/3px;
  height: 6px;
  left: 4px;
  position: absolute;
  top: 1px;
  width: 12px;
}

.group .subtract {
  height: 5px;
  left: 15px;
  position: absolute;
  top: 0;
  width: 5px;
}

.group .img {
  height: 5px;
  left: 0;
  position: absolute;
  top: 0;
  width: 5px;
}

.group .overlap {
  height: 10px;
  left: 6px;
  position: absolute;
  top: 0;
  width: 14px;
}

.group .div {
  background-color: var(--secondary-colorsecondary-2);
  border-radius: 4px;
  height: 8px;
  left: 0;
  position: absolute;
  top: 2px;
  width: 8px;
}

.group .subtract-2 {
  height: 7px;
  left: 9px;
  position: absolute;
  top: 3px;
  width: 5px;
}

.group .ellipse-2 {
  background-color: #FF6934;
  border: 1px solid;
  border-color: #FFFFFF;
  border-radius: 3.5px;
  height: 7px;
  left: 7px;
  position: absolute;
  top: 0;
  width: 7px;
}

.group .subtract-3 {
  height: 7px;
  left: 0;
  position: absolute;
  top: 3px;
  width: 5px;
}
