.creat-post {
  align-items: flex-start;
  border-radius: 16px;
  display: inline-flex;
  flex-direction: column;
  gap: 10px;
  padding: 20px;
  position: relative;
}

.creat-post .main-3 {
  align-items: center;
  display: inline-flex;
  flex: 0 0 auto;
  gap: 20px;
  position: relative;
}

.creat-post .abatars {
  background-color: #f9dfc0;
  border-radius: 20px;
  height: 40px;
  position: relative;
  width: 40px;
}

.creat-post .mask-group {
  height: 32px;
  left: 5px;
  position: absolute;
  top: 4px;
  width: 30px;
}


.creat-post .input-data {
  align-items: flex-start;
  border-radius: 6px;
  display: flex;
  gap: 10px;
  padding: 12px 0px 12px 12px;
  position: relative;
  width: 553px; /* Set a fixed width */
  color: #FFFFFF;
}
  



.creat-post .text-wrapper-4 {
  color: #858EAD;
  font-family: var(--regular-14-font-family);
  font-size: var(--regular-14-font-size);
  font-style: var(--regular-14-font-style);
  font-weight: var(--regular-14-font-weight);
  letter-spacing: var(--regular-14-letter-spacing);
  line-height: var(--regular-14-line-height);
  margin-top: -1px;
  position: relative;
  white-space: nowrap;
  width: fit-content;
}

.creat-post .button-instance {
  flex: 0 0 auto !important;
}

.creat-post.dark-30-off {
  background-color: #FFFFFF;
}

.creat-post.dark-30-on {
  background-color: #262D34;
}

.creat-post.dark-30-off .input-data {
  background-color: #F4F6F8;
}

.creat-post.dark-30-on .input-data {
  background-color: #2C353D;
}

.button {
  all: unset;
  align-items: flex-start;
  background-color: #FF6934;
  border-radius: 6px;
  box-sizing: border-box;
  display: inline-flex;
  gap: 10px;
  padding: 12px 16px;
  position: relative;
}

.button .text-wrapper-3 {
  color: #FFFFFF;
  font-family: "Rubik", Helvetica;
  font-size: 14px;
  font-weight: 500;
  letter-spacing: 0;
  line-height: 20px;
  margin-top: -1px;
  position: relative;
  white-space: nowrap;
  width: fit-content;
}

