}

.text-wrapper-10 {
  font-size: 14px;
  color: #7f265b; /* Link color */
  cursor: pointer;
  text-decoration: underline;
}

/* Style the checkmark when the checkbox is checked */
.checkbox input:checked + .checkmark {
  background-color: #7f265b; /* Checked background color */
  border: 1px solid #7f265b; /* Border color when checked */
}

/* Style the checkmark's inner icon (you can replace this with your custom icon) */
.checkbox input:checked + .checkmark::before {
  content: "";
  position: absolute;
  display: block;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 10px;
  height: 10px;
  background-color: #fff; /* Color of the checkmark */
  border: 2px solid #fff; /* Border color of the checkmark */
  border-radius: 50%;
}

/* Style the "Forgot Password?" link on hover */
.text-wrapper-10:hover {
  text-decoration: none;
}


