.FSPC-login .text-wrapper-11 {
  position: relative;
  background-color: #7f265b;
  color: #fff;
  padding: 10px 20px;
  font-size: 16px;
  border: none;
  cursor: pointer;
  width: 420px;
  transition: all 0.3s ease;
}


.glitch-button {
  position: relative;
  transition: all 0.3s ease-in-out;
}

.glitch-button:hover::before {
  content: attr(data-text);
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  text-shadow: -2px 0 #ff5722, 2px 0 #2196f3;
  animation: glitch-anim 1s ease-in-out 0.3s forwards;
  animation: glitch-anim 1s ease-in-out 0.3s backwards;
}

@keyframes glitch-anim {
  0% {
    transform: translate(0, 0);
  }
  100% {
    transform: translate(4px, 0);
  }
}



